<template lang="pug">
	v-app
		v-navigation-drawer(v-model="drawer" app clipped)
			v-list(dense)
				//- My Dashboard
				v-list-item(to="/dashboard")
					v-list-item-action
						v-icon mdi-home
					v-list-item-content
						v-list-item-title My dashboard

				//- All Courses
				v-list-item(to="/courses")
					v-list-item-action
						v-icon mdi-folder-multiple-outline
					v-list-item-content
						v-list-item-title All courses

				//- Calendar
				v-list-item(to="/calendar")
					v-list-item-action
						v-icon mdi-calendar-account-outline
					v-list-item-content
						v-list-item-title Calendar

				template(v-if="isLoggedIn")
					//- My courses
					v-list-item(to="/mycourses")
						v-list-item-action
							v-icon mdi-folder-account-outline
						v-list-item-content
							v-list-item-title [My courses]

		v-app-bar(app text color="primary" clipped-left dense)
			v-app-bar-nav-icon(@click.stop="drawer = !drawer")
			//- router-link(to="/" color="rgb(200,200,200)")
			//- v-btn(color="transparent" depressed tile to="/")
			v-toolbar-title.headline
				span Y&D
				span.font-weight-light Learning
			//- a.link(href="/ydl-v2-front/#/") Hi
			//- router-link.link(to="/News") Test
			v-spacer
			v-toolbar-items
				v-btn(text to="/").tile Home 
				v-btn(text to="/news").tile News
				v-btn(text to="/about") About

				//- v-menu LOGIN / SIGN UP
				v-menu(bottom offset-y)
					template(v-slot:activator='{ on: menu }')
						//- v-btn(
						//- 	color='primary' 
						//- 	depressed v-on='{ ...menu }' 
						//- 	@click.stop="menuClick = !menuClick") Login / Sign up
						//- 		v-icon(v-if="!menuClick") mdi-menu-up 
						//- 		v-icon(v-if="menuClick") mdi-menu-down 
						//- 		v-list-item(left)
						//- 			v-icon(right large) mdi-account-circle-outline
						v-btn(
							color='primary' 
							depressed v-on='{ ...menu }' 
							@click.stop="menuClick = !menuClick") Hi {{ user.username }}
							v-icon(v-if="!menuClick") mdi-menu-up 
							v-icon(v-if="menuClick") mdi-menu-down 
							v-list-item(left)
								v-icon(right large) mdi-account-circle-outline
					v-list
						//- [if not logged in]
						template(v-if="isLoggedIn")

							//- Login
							v-list-item(to="/login")
								v-list-item-icon
									v-icon mdi-login-variant
								v-list-item-title Login

							//- Sign up
							v-list-item(to="/signup") 
								v-list-item-icon
									v-icon mdi-pencil-plus-outline
								v-list-item-title Sign up

						//- [if logged in]
						template(v-if="isLoggedIn")

							//- My dashboard
							v-list-item(to="/dashboard")
								v-list-item-icon
									v-icon mdi-home
								v-list-item-title [My dashboard]

							//- My profile
							v-list-item(to="/profile")
								v-list-item-icon
									v-icon mdi-account-circle-outline
								v-list-item-title [My profile]

							//- Settings
							v-list-item(to="/settings")
								v-list-item-icon
									v-icon mdi-settings-outline
								v-list-item-title [Settings]

						//- Logout
						//- [if logged in]
						v-list-item(v-if="isLoggedIn" @click='')
							v-list-item-icon.rotate-180
								v-icon mdi-logout-variant
							v-list-item-title [Logout]
		v-content
			router-view
</template>

<script>
import { mapState } from "vuex";
export default {
    name: "App",
    data() {
        return {
            drawer: false,
            menuClick: false
        };
    },
    computed: mapState(["isLoggedIn", "user"])
};
</script>

<style lang="sass">
#app
	//background-color: #1e1e1e
	background-image: url(data:image/png;base64,)
	background-origin: padding-box
	background-position: left top
	background-clip: border-box
	background-repeat: repeat
	background-attachment: scroll
	background-size: auto

html
	// remove scroll bar
	// overflow: hidden

	/* own scrollbar */
	/* scrollbar firefox */
	scrollbar-color: rgb(110, 110, 110) rgb(1, 1, 1)
	/* own scrollbar */
	/* Note: The -webkit-scrollbar is not supported by Firefox or IE and Edge. */
	::-webkit-scrollbar
		/* width */
		width: 12px
	::-webkit-scrollbar-track
		/* Track */
		box-shadow: inset 0 0 5px grey
		border-radius: 8px
	::-webkit-scrollbar-thumb
		/* Handle */
		background: rgb(110, 110, 110)
		border-radius: 10px
	::-webkit-scrollbar-thumb:hover
		/* Handle on hover */
		background: rgb(110, 110, 110)
	::-webkit-scrollbar-track-piece
		/* not handle on */
		background: rgb(1, 1, 1)

	.link
		// color: rgb(250,250,250)!important
.rotate-180
	-webkit-transform: rotate(180deg)
	-moz-transform: rotate(180deg)
	-ms-transform: rotate(180deg)
	-o-transform: rotate(180deg)
	transform: rotate(180deg)
</style>
